import { ethers, providers } from 'ethers';
import { Token, WETH, Fetcher, Route } from '@uniswap/sdk';
import { Subject } from 'rxjs';
import Web3Modal from 'web3modal';

import { Constants } from './Constants';
import { Utils } from './Utils';
import { ClaimService } from './ClaimService';

import landToken from '../contracts/Land.json';
import cornToken from '../contracts/Corn.json';

export class EthereumService {
  constructor() {
    this.events = new Subject();
    this.errors = new Subject();

    this.loadBalancesErrors = {
      count: 0,
      lastError: null,
      blockNumber: 0
    };

    this.web3Modal = new Web3Modal({
      cacheProvider: true,
      providerOptions: Constants.web3ModalProviderOptions
    });

    this.connectWallet();
  }

  reset() {
    if (this.contracts && this.contracts.walletAddress) {
      this.removeEventHandlers(this.contracts.walletAddress);
    }

    this.contracts = {
      isAuthorized: false,
      isPending: false,
      claim: {
        message: 'No Claim Available'
      }
    };

    this.prices = {};

    this.hasError = false;
    this.errorMessage = '';

    this.land = null;
    this.corn = null;
    this.networkConfig = null;

    this.claimService = new ClaimService();
  }

  resetConnectedWallet() {
    this.web3Modal.clearCachedProvider();
  }

  connectWallet() {
    this.web3Modal.connect().then(
      provider => {
        this.provider = new providers.Web3Provider(provider);
        this.connect().then(() => this.events.next());
      },
      error =>
        this.errors.next({
          message: 'Error connecting to wallet, check wallet and try again',
          error: 'Error connecting to wallet'
        })
    );
  }

  connect() {
    try {
      this.signer = this.provider.getSigner();
      return this.initAccount().then(() => {
        this.setupEventHandlers();
      });
    } catch (e) {
      console.log(e);
      this.initError = true;
      return new Promise((resolve, reject) => {});
    }
  }

  initAccount() {
    this.reset();

    return this.provider.getNetwork().then(network => {
      if (network.name === 'unknown') {
        if (network.chainId === Constants.arbitrumKovanConfig.chainId) {
          this.ethersProvider = new ethers.providers.Web3Provider(window.ethereum);
          this.networkConfig = Constants.arbitrumKovanConfig;
        } else {
          if (network.chainId === Constants.xDaiConfig.chainId) {
            this.ethersProvider = new ethers.providers.Web3Provider(window.ethereum);
            this.networkConfig = Constants.xDaiConfig;
          }
        }
      } else {
        this.ethersProvider = ethers.getDefaultProvider(network, Constants.providerOptions);
        this.networkConfig = Constants.networkConfigs[network.name.toUpperCase()];
      }

      if (this.networkConfig) {
        this.land = new ethers.Contract(this.networkConfig.landContractAddress, landToken.abi, this.signer);
        this.corn = new ethers.Contract(this.networkConfig.cornContractAddress, cornToken.abi, this.signer);

        if (this.networkConfig.claimContractAddress) {
          this.claimService.loadClaimContract(this.networkConfig, this.signer);
        }

        this.networkConfig.landToken = new Token(
          this.networkConfig.chainId,
          this.networkConfig.landContractAddress,
          Constants.tokens.land.decimals
        );
        this.networkConfig.cornToken = new Token(
          this.networkConfig.chainId,
          this.networkConfig.cornContractAddress,
          Constants.tokens.corn.decimals
        );

        if (this.networkConfig.loadPrices) {
          this.networkConfig.usdcToken = new Token(this.networkConfig.chainId, this.networkConfig.usdcAddress, 6);
          this.networkConfig.wethToken = WETH[this.networkConfig.chainId];
        }
      }
    });
  }

  addToken(tokenName) {
    var options = {};
    switch (tokenName) {
      case 'land':
        Object.assign(options, Constants.tokens.land);
        options.address = this.networkConfig.landContractAddress;
        break;
      case 'corn':
        Object.assign(options, Constants.tokens.corn);
        options.address = this.networkConfig.cornContractAddress;
        break;
      default:
        return;
    }
    window.ethereum
      .request({
        method: 'wallet_watchAsset',
        params: {
          type: 'ERC20',
          options: options
        }
      })
      .then(
        added =>
          added
            ? this.events.next({ message: 'Token Added' })
            : this.events.next({ message: 'Cancelled Add Token', error: 'Token add cancelled' }),
        error => this.errors.next({ message: 'Failed to add token', error: error })
      );
  }

  loadPrices() {
    if (!this.provider || !this.provider.network || !this.networkConfig) {
      return;
    }

    this.provider.getGasPrice().then(price => {
      this.prices.gas = Utils.formatUnits(price, 'gwei');
    });

    if (!this.networkConfig || !this.networkConfig.loadPrices) {
      this.prices.landUsd = 0.1;
      this.prices.cornUsd = 0.1;
    } else {
      this.signer.getAddress().then(address =>
        Promise.all([
          Fetcher.fetchPairData(this.networkConfig.wethToken, this.networkConfig.usdcToken, this.ethersProvider),
          Fetcher.fetchPairData(this.networkConfig.landToken, this.networkConfig.wethToken, this.ethersProvider),
          Fetcher.fetchPairData(this.networkConfig.cornToken, this.networkConfig.wethToken, this.ethersProvider)
        ]).then(
          response => {
            const wethUsdcPair = response[0];
            const landWethPair = response[1];
            const cornWethPair = response[2];
            const landRoute = new Route([wethUsdcPair, landWethPair], this.networkConfig.usdcToken);
            const cornRoute = new Route([wethUsdcPair, cornWethPair], this.networkConfig.usdcToken);

            this.prices.landUsd = landRoute.midPrice.invert().toSignificant(4);
            this.prices.cornUsd = cornRoute.midPrice.invert().toSignificant(4);
          },
          error => {
            this.handleLoadPricesError(error);
          }
        )
      );
    }
  }

  loadBalances() {
    if (!this.provider.network) {
      this.errors.next({
        message: 'Error loading network',
        error: 'Error loading network info from provider'
      });
      return new Promise((resolve, reject) => {});
    }

    if (!this.networkConfig) {
      this.errors.next({
        message: 'Error loading farm, unsupported network: ' + this.provider.network.name,
        error: 'Unsupported network: ' + this.provider.network.name
      });
      return new Promise((resolve, reject) => {});
    }

    return this.signer.getAddress().then(address =>
      Promise.all([
        //0 wallet address
        address,
        //1 ETH balance
        this.provider.getBalance(address),
        //2 getAddressDetails [0] blockNumber [1] cropBalance [2] toHarvest, [3] MaturityBoost, [4] CompostBoost, [5]globalAllocatedAmount, [6] globalCompostedAmount
        this.corn.getAddressDetails(address),
        //3 struct Farm [0]Amount of LAND [1] CORN composted [2] Farm built, [3] Last harvested [4] Delegated Harvester
        this.corn.farms(address),
        //4 getAddressTokenDetails [0] Block [1] isCornOperator [2] landBalance [3] globalNumberOfFarms [4] myRatio (MyComposted / LandInFarm)
        this.corn.getAddressTokenDetails(address),
        //5 [0]maxCompostBoost, [1]maxMaturityBoost, [2]maxGrowthCycle, [3]maturityBoostExtension
        this.corn.getConstantDetails(),
        //6 [0]globalTotalFarms, [1]globalRatio (globalComposted / globalLandInFarm), [2]globalAverageRatio, (globalComposted / globalLandInFarm) / GlobalTotalFarms [3]globalAllocatedAmount, [4]globalCompostedAmount
        this.corn.getGlobalDetails()
      ]).then(
        response => {
          this.contracts = {
            networkConfig: this.networkConfig,
            isConnected: true,
            walletAddress: response[0],
            delegatedAddress: response[0],
            ethBalance: Utils.formatUnits(response[1]),
            blockNumber: parseInt(response[2] ? response[2][0] : null),
            cornBalance: Utils.formatUnits(response[2] ? response[2][1] : null),
            cornToHarvest: Utils.formatUnits(response[2] ? response[2][2] : null),
            farmMaturityBoost: Utils.formatUnits(response[2] ? response[2][3] : null, 4),
            farmCompostBoost: Utils.formatUnits(response[2] ? response[2][4] : null, 4),
            landInFarm: Utils.formatUnits(response[3] ? response[3][0] : null),
            cornComposted: Utils.formatUnits(response[3] ? response[3][1] : null),
            farmBuiltBlockNumber: parseInt(response[3] ? response[3][2] : null),
            lastHarvestBlockNumber: parseInt(response[3] ? response[3][3] : null),
            isAuthorized: response[4] ? response[4][1] : null,
            landBalance: Utils.formatUnits(response[4] ? response[4][2] : null),
            myRatio: Utils.formatUnits(response[4] ? response[4][4] : null, 4),
            maxGrowthCycle: parseInt(response[5] ? response[5][2] : null),
            globalRatio: Utils.formatUnits(response[4] ? response[4][5] : null, 4),
            globalNumberOfFarms: Utils.formatUnits(response[6] ? response[6][0] : null, 0),
            globalLandInFarm: Utils.formatUnits(response[6] ? response[6][3] : null),
            globalComposted: Utils.formatUnits(response[6] ? response[6][4] : null),
            burnRequiredTo10x: 1
          };

          // Only calculate this when we have at least one farm globally to avoid division by zero errors
          if (this.contracts.globalNumberOfFarms !== 0) {
            this.contracts.globalAverageLandInFarms =
              this.contracts.globalLandInFarm / this.contracts.globalNumberOfFarms;
            this.contracts.globalAverageComposted = this.contracts.globalComposted / this.contracts.globalNumberOfFarms;
          }

          // TODO: Just initialise the variable to avoid error ... Sparta to find a proper fix
          this.contracts.growingCycleCountDown = '';

          // Only calculate this stuff when there is a farm
          if (this.contracts.landInFarm > 0) {
            // Calculates the number of blocks until the end of the growing cycle:
            this.contracts.endOfGrowingCycle = this.contracts.lastHarvestBlockNumber + this.contracts.maxGrowthCycle;
            // Calculates the approx. number of seconds until the end of the growing cycle
            const networkBlockTime = 13.5; // 4; // 13.5; // - mainnet
            this.contracts.growingCycleCountDownSecs =
              networkBlockTime * (this.contracts.endOfGrowingCycle - this.contracts.blockNumber);
            // Decides how to present the countdown in hours or complete message
            if (this.contracts.growingCycleCountDownSecs > 0) {
              this.contracts.growingCycleCountDown = this.calculateCountdown(this.contracts.growingCycleCountDownSecs);
            } else {
              this.contracts.growingCycleCountDown = 'COMPLETE';
            }

            // Calculate how my corn/land compost ratio compares to the global average
            this.contracts.myRatioVsGlobalRatio = parseFloat(
              this.contracts.myRatio / this.contracts.globalRatio,
              4
            ).toFixed(2);

            // x 10 composting boost calc. x10 = 9 * global composted average
            if (this.contracts.farmCompostBoost < 10) {
              var averageMaxTo10x = (9 * this.contracts.globalComposted) / this.contracts.globalNumberOfFarms;
              var myFarmMultiple = this.contracts.landInFarm / this.contracts.globalAverageLandInFarms;
              this.contracts.maxTo10x = averageMaxTo10x * myFarmMultiple;
              this.contracts.burnRequiredTo10x = this.contracts.maxTo10x - this.contracts.cornComposted;
            } else {
              this.contracts.burnRequiredTo10x = 'OVER COMPOSTED';
            }
          }
        },
        error => {
          this.handleLoadBalancesError(error);
        }
      )
    );
  }

  calculateCountdown(totalSeconds) {
    const minutes = ('0' + Math.floor((totalSeconds / 60) % 60)).slice(-2);
    const hours = ('0' + Math.floor((totalSeconds / (60 * 60)) % 24)).slice(-2);
    const days = ('0' + Math.floor(totalSeconds / (60 * 60 * 24))).slice(-2);

    return {
      days,
      hours,
      minutes
    };
  }

  setShouldCheckClaim(shouldCheckClaim) {
    this.shouldCheckClaim = shouldCheckClaim;
  }

  checkClaim() {
    if (!this.shouldCheckClaim || !this.signer || !this.provider || !this.provider.network || !this.networkConfig) {
      return;
    }

    this.signer.getAddress().then(address => {
      this.claimService.checkClaim(address);
    });
  }

  sendClaim() {
    this.claimService.sendClaim(this.contracts.walletAddress).then(
      response => {
        this.events.next({ message: response, pending: true });
      },
      error => this.errors.next({ message: 'Claim failed : ' + error, error: error })
    );
  }

  handleLoadPricesError(error) {
    if (error && error.reason) {
      console.log('Error loading prices: ' + error.reason);
    } else {
      console.log(error);
    }
  }

  handleLoadBalancesError(error) {
    if (!this.contracts.blockNumber) {
      this.errors.next({ message: 'Error loading farm details', error: error });
    }

    if (this.loadBalancesErrors.blockNumber === this.contracts.blockNumber) {
      // "ignore same block as previous error"
      return;
    }

    var err = error && error.message ? error.message : 'unknown';

    if (
      this.loadBalancesErrors.blockNumber + 1 === this.contracts.blockNumber &&
      this.loadBalancesErrors.lastError === err
    ) {
      if (this.loadBalancesErrors.count > 3) {
        // >3 continuous error - report
        this.loadBalancesErrors = { count: 1, lastError: err, blockNumber: this.contracts.blockNumber };
        this.errors.next({ message: 'Error loading latest farm details, information may be stale', error: error });
      } else {
        // <=3 continuous error - capture
        this.loadBalancesErrors.count++;
        this.loadBalancesErrors.lastError = err;
        this.loadBalancesErrors.blockNumber = this.contracts.blockNumber;
      }
    } else {
      // non-continuous error - reset
      this.loadBalancesErrors = { count: 1, lastError: err, blockNumber: this.contracts.blockNumber };
    }
  }

  removeEventHandlers() {
    this.provider.provider.removeAllListeners();
    this.provider.removeAllListeners();
    this.corn.removeAllListeners();
  }

  setupEventHandlers() {
    var refresh = () => {
      this.loadBalances().then(() => {
        this.events.next();
      });
      this.loadPrices();
      this.checkClaim();
    };

    var update = message =>
      this.loadBalances().then(() => {
        this.events.next({ message: message });
      });

    this.provider.provider.on('accountsChanged', accounts => {
      if (!accounts) {
        this.resetConnectedWallet();
        window.location.reload();
      } else {
        this.connect().then(() => {
          refresh();
        });
      }
    });

    this.provider.provider.on('chainChanged', networkId => {
      if (!networkId) {
        this.resetConnectedWallet();
      }

      window.location.reload();
      this.connectWallet();
    });

    if (this.networkConfig && this.networkConfig.cornContractAddress) {
      this.provider.on('block', currentBlock => {
        if (this.contracts.blockNumber && this.contracts.blockNumber !== currentBlock) {
          this.contracts.blockNumber = currentBlock;
          refresh();
        }
      });

      this.signer.getAddress().then(address => {
        this.corn.on(
          {
            address: address,
            topics: [ethers.utils.id('AuthorizedOperator(address,address)')]
          },
          (cornContractAddress, walletAddress) => {
            if (walletAddress === address) {
              update('Planning permission granted');
            }
          }
        );

        this.corn.on(
          {
            address: address,
            topics: [ethers.utils.id('Allocated(address,uint256,address,uint256,uint256)')]
          },
          (walletAddress, currentBlock, delegatedAddress, plantedAmount, burnedAmountIncrease) => {
            if (walletAddress === address) {
              update('Your LAND is allocated to farm');
            }
          }
        );

        this.corn.on(
          {
            address: address,
            topics: [ethers.utils.id('Released(address,uint256,uint256)')]
          },
          (walletAddress, landInFarm, burnedAmountDecrease) => {
            if (walletAddress === address) {
              update('Your LAND has been released from a farm');
            }
          }
        );

        this.corn.on(
          {
            address: address,
            topics: [ethers.utils.id('Harvested(address,uint256,address,address,uint256,uint256)')]
          },
          (walletAddress, blockNumber, delegatedAddress, preBlockNumber, cornToHarvest) => {
            if (walletAddress === address) {
              update('Your CORN has been harvested');
            }
          }
        );

        this.corn.on(
          {
            address: address,
            topics: [ethers.utils.id('Composted(address,address,uint256)')]
          },
          (walletAddress, delegatedAddress, fertilizedAmount) => {
            if (walletAddress === address) {
              update('Your CORN has been composted & used to fertilize your farm');
            }
          }
        );
      });
    }

    refresh();
  }

  approveContract() {
    this.loadBalances().then(() => {
      this.land.authorizeOperator(this.networkConfig.cornContractAddress, { from: this.contracts.walletAddress }).then(
        () => this.events.next({ message: 'Planning permission is pending', pending: true }),
        error => this.errors.next({ message: 'Error providing planning permission', error: error })
      );
    });
  }

  sanitizeAmount(amount) {
    if (!amount || isNaN(amount)) {
      amount = '0';
    } else if (typeof amount === 'number') {
      amount = ethers.utils.formatEther(amount);
    }
    return ethers.utils.parseEther(amount);
  }

  allocateLand(amount) {
    var sanitizedAmount = this.sanitizeAmount(amount);

    if (sanitizedAmount <= 0) {
      this.errors.next({
        message: 'Error allocating LAND - Please enter a valid amount',
        error: 'Invalid value entered to allocate'
      });
      return;
    }

    this.loadBalances().then(() => {
      this.corn.allocate(this.contracts.walletAddress, sanitizedAmount).then(
        () => this.events.next({ message: 'Your LAND is being allocated to a farm', pending: true }),
        error =>
          this.errors.next({
            message: 'Error allocating LAND - To make a bigger farm, ensure you release LAND first',
            error: error
          })
      );
    });
  }

  releaseLand() {
    this.loadBalances().then(() => {
      this.corn.release().then(
        () => this.events.next({ message: 'Pending release of LAND', pending: true }),
        error => this.errors.next({ message: 'Error releasing LAND', error: error })
      );
    });
  }

  harvestCorn() {
    this.loadBalances().then(() => {
      this.provider.getBlockNumber().then(blockNumber => {
        this.corn.harvest(this.contracts.walletAddress, this.contracts.walletAddress, blockNumber).then(
          () => this.events.next({ message: 'Your CORN harvest is pending', pending: true }),
          error => this.errors.next({ message: 'Error harvesting CORN', error: error })
        );
      });
    });
  }

  compostCorn(amount) {
    var sanitizedAmount = this.sanitizeAmount(amount);

    if (sanitizedAmount <= 0) {
      this.errors.next({
        message: 'Error composting CORN - Please enter a valid amount',
        error: 'Invalid value entered to compost'
      });
      return;
    }

    this.loadBalances().then(() => {
      this.corn.compost(this.contracts.walletAddress, sanitizedAmount).then(
        () => this.events.next({ message: 'Composting CORN is pending', pending: true }),
        error => this.errors.next({ message: 'Error composting CORN', error: error })
      );
    });
  }
}
